<template>
  <div class="update-holder">
    <div class="dropdown">
      <button class="btn btn-primary dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
        {{!title ? 'Show Menu' : title}}
      </button>
      <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
        <span class="dropdown-item" v-for="item, index in menu" @click="makeActive(item.type, item.title)">{{item.title}}</span>
      </div>
    </div>
    <div class="sidebar">
      <span class="header">
        Personal Settings
      </span>
      <span class="item" v-bind:class="{'make-active': item.type === activeType}" v-for="item, index in menu" v-if="menu !== null" @click="makeActive(item.type, item.title)">
        {{item.title}}
      </span>
    </div>
    <div class="content">
      <profile v-if="activeType === 'profile'"></profile>
      <account v-if="activeType === 'account'"></account>
      <payment v-if="activeType === 'payment_method'"></payment>
      <billing-information v-if="activeType === 'billing_information'"></billing-information>
      <merchant v-if="activeType === 'merchant'"></merchant>
      <notification v-if="activeType === 'notification'"></notification>
    </div>
  </div>

</template>
<style scoped>
.update-holder{
  width: 100%;
  float: left;
  margin-top: 25px;
  margin-bottom: 200px;
}

.sidebar{
  width: 25%;
  float: left;
  border-top: solid 1px #ddd;
  border-right: solid 1px #ddd;
  border-left: solid 1px #ddd;
  border-top-right-radius: 2px;
  border-top-left-radius: 2px;
  min-height: 40px;
  overflow-y: hidden;
}
.sidebar .header{
  height: 40px;
  float: left;
  width: 100%;
  line-height: 40px;
  padding-left: 10px;
  border-bottom: solid 1px #ddd;
  background: #eee;
}
.sidebar .item{
  height: 40px;
  float: left;
  width: 100%;
  line-height: 40px;
  padding-left: 10px;
  border-bottom: solid 1px #ddd;
}
.item:hover{
  cursor: pointer;
  border-left: solid 2px #22b173 !important;
}
.make-active{
  border-left: solid 2px #22b173 !important;
}
.content{
  width: 75%;
  float: left;
  min-height: 50px;
  overflow-y: hidden;
}
.dropdown{
  width: 100%;
  display: none;
}
.custom-block-btn{
  width: 100% !important;
  text-align: right;
  padding-right: 10px;
}
@media screen and (max-width: 992px){
  .sidebar{
    display: none;
  }
  .content{
    width: 100%;
  }
  .dropdown{
    display: block;
  }
  .btn-primary.active, .btn-primary:active, .show>.btn-primary.dropdown-toggle{
    background: #028170;
    border-color: #028170;
  }
}
</style>
<script>
import ROUTER from '../../../router'
import AUTH from '../../../services/auth'
import axios from 'axios'
import CONFIG from '../../../config.js'
export default {
  mounted(){
    AUTH.checkPlan()
    if(this.parameter !== null){
      let flag = false
      for (var i = 0; i < this.menu.length; i++) {
        if(this.parameter === this.menu[i].type){
          flag = true
          this.makeActive(this.menu[i].type)
        }
      }
      if(flag === false){
        this.makeActive('profile')
      }
    }else{
      this.makeActive('profile')
    }
  },
  data(){
    return {
      user: AUTH.user,
      tokenData: AUTH.tokenData,
      config: CONFIG,
      menu: [
        {title: 'Profile', type: 'profile'},
        {title: 'Account', type: 'account'},
        {title: 'Payment Accounts', type: 'payment_method'},
        {title: 'Billing Information', type: 'billing_information'},
        {title: 'Merchant Setting', type: 'merchant'},
        {title: 'Notifications', type: 'notification'}
      ],
      activeType: 'profile',
      parameter: this.$route.params.parameter,
      title: 'Show Menu'
    }
  },
  components: {
    'profile': require('components/increment/settings/Profile.vue'),
    'account': require('components/increment/settings/Account.vue'),
    'payment': require('components/increment/settings/Payment.vue'),
    'billing-information': require('components/increment/settings/BillingInformation.vue'),
    'merchant': require('components/increment/settings/Merchant.vue'),
    'notification': require('components/increment/settings/Notification.vue')
  },
  methods: {
    redirect(path){
      ROUTER.push(path)
    },
    makeActive(type, title){
      this.activeType = type
      this.title = title
    }
  }
}
</script>



